import React from 'react'
import { TabBar } from 'antd-mobile';
import './detail.css'

const Detail = (props) => {
    return (
        <div className="detail">
            <i style={{ fontSize: 25 }} className='iconfont icon-Back' onClick={() => props.history.push('/')}></i>
            <div>
                <img style={{ height: 500, width: '92%', marginLeft: 10, marginTop: 10 }} src='https://gw.alicdn.com/bao/uploaded/i1/50983440/TB1mWIVp7yWBuNjy0FpXXassXXa_!!0-item_pic.jpg_500x500q90.jpg_.webp' />
            </div>
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="home"
                        icon={
                            <i className='iconfont icon-store'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-store-checked'></i>
                        }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-atm'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-atm'></i>
                        }
                        title="客服"
                        key="service"
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-star'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-star-checked'></i>
                        }
                        title="收藏"
                        key="collection"
                    >
                    </TabBar.Item>
                    <TabBar.Item

                        icon={
                            <i></i>
                        }
                        title="加入购物车"
                        key="cart"
                        class="left"
                        style={{ backgroundColor: "black" }}

                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i></i>
                        }
                        title="立即购买"
                        key="buy"
                        class="right"
                    >
                    </TabBar.Item>
                </TabBar>
            </div>
        </div>
    )
}

export default Detail


